//  banner
.j-one-banner {
  &__time {
    font-weight: 500;
    font-size: 17px;
    @include respond(team-md) {
      font-size: 20px;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 22px;

    @include respond(team-md) {
      margin-bottom: 10px;
      font-size: 35px;
    }
  }
}

//  + 
.j-one-user {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 20px;

  &__img-box {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 15px;

    & img {
      width: 100%;
      height: 100%;
    }
  }
}

//   ( +  + )
.j-one-meetup-wrap {
  &__img-box {
    height: 350px;
    margin-bottom: 40px; //

    & img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

//   + 
.j-one-section {
  margin-bottom: 50px; //section 

  &__title-wrap {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-top: 15px;
    margin-bottom: 30px;
  }

  &__content {
    font-size: 32px;
    font-weight: bold;
  }

  &__type {
    font-size: 20px;
    font-weight: bold;
  }

  &__paragraph {
    font-size: 20px;
  }

  // 
  &__attendee-wrap {
    //   
    padding-left: 20px;
    padding-right: 20px;

    @include respond(team-sm) {
      padding-left: 0px;
      padding-right: 0px;
    }
  }
}

// 
.j-one-attendee {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 220px;

  background-color: $meetup-lightgreen2;

  max-width: 155px;

  margin-bottom: 30px;

  &__img-wrap {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;

    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__name {
    margin-top: 20px;
    font-size: 20px;
    font-weight: 600;
  }

  &__position {
    font-size: 15px;
    font-weight: 600;
    color: gray;
  }
}

// 
.j-one-recommend {
  // NOTE 1021 for Link
  display: block;
  color: black;

  background-color: $meetup-lightgreen2;
  padding: 25px;
  border: 5px solid $meetup-lightgreen2;
  cursor: pointer;
  margin-bottom: 20px;
  @include respond(team-lg) {
    margin-bottom: 0;
    height: 100%; //   
  }

  // NOTE  border
  transition: border 0.2s;

  &:hover {
    background-color: $team-white;
    border: 5px solid #96bb7c;

    // NOTE 1021 for Link
    text-decoration: none;
    color: black;

    // NOTE hover  userImg
    & .j-one-count__userImg {
      border: 3px solid $team-white;
    }
  }
  // padding-top: 25px;
  // padding-bottom: 25px;
  // margin-right: 20px;

  &__time {
    font-weight: 500;
  }
  &__title {
    margin-top: 1px;
    font-weight: bold;
    font-size: 23px;
  }
  &__location {
    display: flex;
    margin-top: 20px;
    margin-bottom: 10px;

    //NOTE 1022 added
    align-items: baseline;

    i {
      margin-left: 6px;
      margin-right: 13px;
    }
  }

  &__data {
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__count-wrap {
    display: flex;
    justify-content: space-between;
    // FIX
    margin-top: 20px;
  }
}

.j-one-count {
  display: flex;
  align-items: center;
  // NOTE 1023 added  btn  
  margin-right: 6px;

  &__users {
    display: flex;
    align-items: center;
  }
  &__userImg {
    box-sizing: content-box; //default
    height: 50px;
    width: 50px;
    border-radius: 50%;
    //  NOTE j-one-recommend hover  userImg (j-one-recommend )
    //   j-one-recommend 
    //  j-one-recommend  hover   
    border: 3px solid $meetup-lightgreen2;

    &:not(:last-child) {
      margin-right: -20px;
    }
  }

  &__plus {
    font-weight: 600;
    font-size: 20px;
  }
}

.j-one-btn {
  color: $team-white;
  background-color: #025930;
  border-radius: 100px;
  font-weight: bold;
  font-size: 16px;
  margin: 9px 0px; //
  padding: 0 15px;

  // NOTE 1021 fix
  // width: min-content;

  transition: all 0.2s;
  &:hover {
    color: $team-white;
    background: #96bb7c;
  }

  &--bottom {
    background-color: #141719;

    // add
    font-size: 15px;
    padding: 10px 25px;

    @include respond(team-md) {
      font-size: 18px;
      padding: 10px 35px;
    }

    &:hover {
      color: $team-white;
      background: linear-gradient(
          0deg,
          rgba(255, 255, 255, 0.3),
          rgba(255, 255, 255, 0.3)
        ),
        #000000;
    }
  }
}

//   ( +  + )
.j-one-side {
  padding: 45px 25px 35px;
  border: 3px solid $team-primary;
  // top: 60px; //
  top: 116px; // 60+56 nav height

  margin-bottom: 80px;
  @include respond(team-lg) {
    margin-bottom: 0px; // 8 4  margin side 
    padding: 35px 20px 25px;
  }

  // 
  &__info-wrap {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  //  + 
  &__data-box {
    display: flex;
    align-items: baseline;
    margin-bottom: 8px;
    font-weight: 600;

    i {
      margin-right: 15px;
    }

    //   icon
    i.fix-2 {
      margin-left: 2px;
    }
  }

  // 
  &__map {
    height: 250px;
  }
}

//  bottom  sticky bar
.j-one-attend {
  bottom: 0;
  color: $team-white;
  background-color: $team-primary;

  padding: 0; //NOTE  bs container padding 

  font-size: 14px;
  @include respond(team-md) {
    font-size: 16px;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    font-weight: bold;
    font-size: 15px;

    @include respond(team-sm) {
      font-size: 16px;
    }

    @include respond(team-md) {
      flex-direction: row;
      font-size: 24px;
    }
  }
}

// NOTE 1021 

//  footer  HTML 
.fake-footer {
  color: $team-white;
  background-color: $team-secondary;
  height: 200px;
  font-size: 80px;
  font-weight: bold;
  display: flex;
  justify-content: center;
}
